.header {
  height: 100vh;
  width: 100vw;
  background-image: url("./images/hero.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;

  &__container {
    width: 100%;
  }

  &__background {
    width: 100%;
    background-color: rgba(#fff, 0.5);
  }
}
